<template>
   <f7-navbar>
  	  <f7-nav-left>
  	    <f7-link icon="icon-bars color-white" open-panel="left"></f7-link>
  	  </f7-nav-left>

  	  <f7-nav-center sliding>
  	    <img id="logo" :src="logo" >
  	  </f7-nav-center>
  	  <f7-nav-right>
  	    <!-- <f7-link icon="icon-forward" open-panel="right"></f7-link> -->
  	    <div class="icon-cell" v-if="!isAuthenticated()">
  	      <!-- <f7-link> -->
  	      	<a @click="$root.reroute('home')" >
  		        <i class="f7-icons size-50">home</i>
  	      	</a>

  	      <!-- </f7-link> -->
  	    </div>
        <div class="cart icon-cell" 
             v-if="isAuthenticated()"
             @click="$root.reroute('cart')">
          <img src="../../icons/cart.svg" width="35" height="35">
        </div>
  	  </f7-nav-right>	
  </f7-navbar>

</template>


<script>
  module.exports = {
    data: function() {
      return {
        logo: this.$root.images.logo,
      }
    },
    methods: {
      goToCart: function() {
      this.$f7.popup("#cart-screen")
      },
      isAuthenticated: function() {
        return this.$store.getters['user/isAuthenticated']
      }

    }
  }	
</script>

<style scoped>
  img#logo {
    /*height: 100%;*/
    height: 40px;
    width: auto;
  }	

  i {
    color: white;
  }
</style>


