<template>
  <f7-page>
  <div class="profile" v-if="isAuthenticated()">
    <img id="profilepic" v-bind:src="getProfilePic()" >
    <hr>
    <p class="text-center">{{$store.state.user.username}}</p>
    <hr>
  </div>   

  <f7-list>
    <f7-list-item @click="$f7.mainView.router.load({url: '/howitbegan/'})" link="/" title="How it Began" class="close-panel"></f7-list-item>
    <!-- This links to  an external blog -->
    <f7-list-item @click="visitBlog()" data-url="https://donnabelllashes.wordpress.com/" title="Blog" class="navLink close-panel"></f7-list-item>
    <f7-list-item @click="$f7.mainView.router.load({url: '/press/'})" link="/" title="Press" class="close-panel"></f7-list-item>
    <f7-list-item @click="$f7.mainView.router.load({url: '/howtodesign/'})" link="/" title="How to Design" class="close-panel"></f7-list-item>
    <f7-list-item @click="$f7.mainView.router.load({url: '/getstarted/'})" link="/" title="Get Started" class="close-panel"></f7-list-item>

    <f7-list-item @click="$f7.mainView.router.load({url: '/checkout/'})" link="/" title="checkout" v-if="isAuthenticated()" class="close-panel"></f7-list-item>


    <!-- <f7-list-item @click="$f7.mainView.router.load({url: '/login/'})" link="/" title="Login" class="close-panel" v-if="!isAuthenticated()"></f7-list-item> -->
    <f7-list-item link="/" title="Login" class="open-login-screen close-panel" v-if="!isAuthenticated()"></f7-list-item>
    <f7-list-item @click="$f7.mainView.router.load({url: '/register/'})" link="/" title="Register" class="close-panel" v-if="!isAuthenticated()"></f7-list-item>


    <f7-list-item @click="$f7.popup('#cart-screen')" link="/" title="Shopping Cart" v-if="isAuthenticated()" class="close-panel"></f7-list-item>

    <f7-list-item @click="logout()" link="/" title="Logout" class="close-panel" v-if="isAuthenticated()"></f7-list-item>

  </f7-list>
  </f7-page>
</template>

<script>

  export default {
    methods: {
      getProfilePic: function() {
        return this.$store.getters['user/profilePic']
      },
      isAuthenticated: function() {
        console.log(this.$store)
        return this.$store.getters['user/isAuthenticated']
      },
      logout: function() {
        // call the logout method on the store
        this.$store.commit('user/logout')
        this.$root.reroute('home')
      },
      visitBlog: function() {
        console.log('visiting blog')
        this.$root.visitBlog()
      }
    }
  }
</script>

<style scoped>
  .profile {
    margin: 10px 0;
  }
  #profilepic {
    display: block;
    width: 50%;
    height: auto;
    border-radius: 100%;
    margin: 0 auto;
  }
  .text-center {
    text-align: center;
  }
</style>



