<template>
       <button id="buyNowBtn" @click="payNow"> Buy Now !</button>
</template>


<script>

export default {
    methods: {
        onF7Init: function() {
              var clientIDs = {
                "PayPalEnvironmentProduction": "AaCGnn6yujvN5SF-8ECGNfW6Yp4G2XKJuJg8J8InfjIVTCKDHmxqWV7c5kw3PhcngJkUYvzA2p0Bkic-",
                "PayPalEnvironmentSandbox": "AVDbedcZXmz5ES871LJLu6Ava5RYyLfiaman-__8HGBHvlj9hIRyDEG0E-IxXj-AMoydA9jxps_yJTR5"
              };

              window.PayPalMobile.init(clientIDs, this.onPayPalMobileInit);

        }
    },
    onSuccesfulPayment : function(payment) {
      console.log("payment success: " + JSON.stringify(payment, null, 4));
    },
    // This code is only used for independent card.io scanning abilities
    onCardIOComplete: function(card) {
      console.log("Card Scanned success: " + JSON.stringify(card, null, 4));
    },
    onAuthorizationCallback : function(authorization) {
      console.log("authorization: " + JSON.stringify(authorization, null, 4));
    },
    createPayment : function () {
      // for simplicity use predefined amount
      // optional payment details for more information check [helper js file](https://github.com/paypal/PayPal-Cordova-Plugin/blob/master/www/paypal-mobile-js-helper.js)
      var paymentDetails = new PayPalPaymentDetails("50.00", "0.00", "0.00");
      var payment = new PayPalPayment("50.00", "USD", "Awesome Sauce", "Sale", paymentDetails);
      return payment;
    },
    configuration : function () {
      // for more options see `paypal-mobile-js-helper.js`
      var config = new PayPalConfiguration({merchantName: "My test shop", merchantPrivacyPolicyURL: "https://mytestshop.com/policy", merchantUserAgreementURL: "https://mytestshop.com/agreement"});
      return config;
    },
    payNow: function() {
        window.PayPalMobile.renderSinglePaymentUI(this.createPayment(), this.onSuccesfulPayment, this.onUserCanceled);        
    },
    onPrepareRender : function() {
      // buttons defined in index.html
      //  <button id="buyNowBtn"> Buy Now !</button>
      //  <button id="buyInFutureBtn"> Pay in Future !</button>
      //  <button id="profileSharingBtn"> ProfileSharing !</button>
      //  <button id="cardScanBtn">Advanced: Use card.io scan only</button>
      var buyNowBtn = document.getElementById("buyNowBtn");
      var buyInFutureBtn = document.getElementById("buyInFutureBtn");
      var profileSharingBtn = document.getElementById("profileSharingBtn");
      var cardScanBtn = document.getElementById("cardScanBtn");

      buyNowBtn.onclick = function(e) {
        // single payment
        window.PayPalMobile.renderSinglePaymentUI(this.createPayment(), this.onSuccesfulPayment, this.onUserCanceled);
      };

      buyInFutureBtn.onclick = function(e) {
        // future payment
        window.PayPalMobile.renderFuturePaymentUI(this.onAuthorizationCallback, this.onUserCanceled);
      };

      profileSharingBtn.onclick = function(e) {
        // profile sharing
        window.PayPalMobile.renderProfileSharingUI(["profile", "email", "phone", "address", "futurepayments", "paypalattributes"], this.onAuthorizationCallback, this.onUserCanceled);
      };
      
      cardScanBtn.onclick = function(e) {
        // card.io scanning independent of paypal payments. 
        // This is used for cases where you only need to scan credit cards and not use PayPal as funding option.
        CardIO.scan({
                     "requireExpiry": true,
                     "requireCVV": false,
                     "requirePostalCode": false,
                     "restrictPostalCodeToNumericOnly": true
                   },
                   this.onCardIOComplete,
                   app.onUserCanceled
                 );
       };
    },
    onPayPalMobileInit : function() {
      // must be called
      // use PayPalEnvironmentNoNetwork mode to get look and feel of the flow
      window.PayPalMobile.prepareToRender("PayPalEnvironmentSandbox", app.configuration(), app.onPrepareRender);
    },
    onUserCanceled : function(result) {
      console.log(result);
    }
}
</script>


