<template>
	<div id="swiper-container" class="swiper-container">
	    <!-- Slides wrapper -->
	    <div class="swiper-wrapper">
	        <!-- Slides -->
	        <div class="swiper-slide">
		        <img class="lash-image" src="../../images/samples/01.jpg" width="100%">
	        </div>
	        <div class="swiper-slide">
		        <img class="lash-image" src="../../images/samples/02.jpg" width="100%">	        	
	        </div>

	        <div class="swiper-slide">
		        <img class="lash-image" src="../../images/samples/03.jpg" width="100%">
	        </div>

	        <div class="swiper-slide">
		        <img class="lash-image" src="../../images/samples/04.jpg" width="100%">
	        </div>

	        <div class="swiper-slide">
		        <img class="lash-image" src="../../images/samples/05.png" width="100%">
	        </div>
	        <div class="swiper-slide">
		        <img class="lash-image" src="../../images/samples/06.png" width="100%">
	        </div>

	    </div>
	    <!-- Pagination, if required -->
	    <!-- <div class="swiper-pagination"></div> -->
	    <div class="swiper-button-prev"></div>
	    <div class="swiper-button-next"></div>	    
	</div>
</template>

<script>
	module.exports = {
		methods: {
			onF7Init: function(f7) {
				this.f7 = f7;
				this.slider = this.f7.swiper('.swiper-container', {
				    speed: 500,
				    spaceBetween: 50,
				    pagination:'.swiper-pagination',
	  			    paginationHide: false,
				    paginationClickable: true,
				    nextButton: '.swiper-button-next',
				    prevButton: '.swiper-button-prev',
				    autoplay: 6000,
				    autoplayDisableOnInteraction: false,
				});
				// window.setInterval((function() {this.slider.slideTo()}).bind(this), 5000)

				 // The swiper height needs to match the 4/3 aspect ratio of the captured images
				document.querySelector("#swiper-container").style.height = screen.width * 3 / 4 + "px";
			},
		},
		created: function() {
			// console.log('I have been created!');
			// console.log(this.$f7);

			// document.querySelector("swiper");
		},
		data: function() {
			return {
				f7: null,
				slider: null
			}
		}
	}
</script>

<style scoped>
	.swiper-container {
		height: 300px;
	}

	.lash-image {
		max-height: 100%;
		width: auto;
	}
	
	
	.swiper-slide {
	  background: #fff;
	  box-sizing: border-box;
	  border: 1px solid #ccc;
	}

	.swiper-slide span {
	  text-align:center;
	  display:block;
	  margin: 20px;
	  font-size:21px;
	}

</style>


