<template>
    <input v-model="value" id="jscolorcomp" class="jscolor"  />
</template>

<script>

    export default {
        data: function() {
            return {
                jscolor: this.$root.jscolor
            }
        },
        props : ['value'],
        methods: {
            onF7Init : function(f7){
                var jscolor = this.jscolor
                var $$ = window.Dom7
                console.log('tartget element')
                console.log(this.$el.querySelector(".jscolor"))
                console.log(document.querySelector(".jscolor"))
                console.log($$('.jscolor'))
                var picker = new jscolor(document.querySelector(".jscolor"))
                console.log('picker')
                console.log(picker)

                // jscolor.installByClassName('jscolor');
                document.querySelector(".jscolor").value = this.value

                $$('.jscolor').change(function(_this){
                    return function(){
                        console.log('changeddd')
                        _this.$emit('input', this.value);
                    }
                }(this));
            },
        },
        watch : {
            "value" : function(val){
                this.$el.jscolor.fromString(val);
            }
        }        
    }
</script>

<style scoped>

    input {
        display: block;
        height: 30px;
        width: 95%;
        margin: 5px auto;
    }

</style>


