<template>
<f7-page>
    <f7-preloader id="preloader" v-if="status=='request_sent'"></f7-preloader>

    <f7-block>
        <f7-login-screen-title>Create Account</f7-login-screen-title>
        <f7-list form>
          <!-- error -->
          <f7-list-item v-if="errors['overall']">
            <span class="error">{{errors['overall']}}</span>
          </f7-list-item>

          <!-- error -->
          <f7-list-item v-if="errors['first_name']">
            <span class="error">{{errors['first_name']}}</span>
          </f7-list-item>

          <f7-list-item>
            <f7-label>First Name</f7-label>
            <f7-input name="firstname" placeholder="Name" type="text" v-model="first_name"></f7-input>
          </f7-list-item>

          <!-- error -->
          <f7-list-item v-if="errors['last_name']">
            <span class="error">{{errors['last_name']}}</span>
          </f7-list-item>
          <f7-list-item>
            <f7-label>Last Name</f7-label>
            <f7-input name="lastname" placeholder="Name" type="text" v-model="last_name"></f7-input>
          </f7-list-item>

          <!-- error -->
          <f7-list-item v-if="errors['email']">
            <span class="error">{{errors['email']}}</span>
          </f7-list-item>          
          <f7-list-item>
            <f7-label>Email</f7-label>
            <f7-input name="email" placeholder="Email" type="email" v-model="email"></f7-input>
          </f7-list-item>

          <!-- error -->
          <f7-list-item v-if="errors['username']">
            <span class="error">{{errors['username']}}</span>
          </f7-list-item>          
          <f7-list-item>
            <f7-label>Username</f7-label>
            <f7-input name="username" placeholder="Username" type="text" v-model="username"></f7-input>
          </f7-list-item>

          <!-- error -->
          <f7-list-item v-if="errors['password']">
            <span class="error">{{errors['password']}}</span>
          </f7-list-item>          
          <f7-list-item>
            <f7-label>Password</f7-label>
            <f7-input name="password" placeholder="Password" type="Password" v-model="password"></f7-input>
          </f7-list-item>

          <!-- error -->
          <f7-list-item v-if="errors['password_repeat']">
            <span class="error">{{errors['password_repeat']}}</span>
          </f7-list-item>          
          <f7-list-item>
            <f7-label>Repeat Password</f7-label>
            <f7-input name="password_repeat" type="password" placeholder="Password" v-model="password_repeat"></f7-input>
          </f7-list-item>

        </f7-list>
        <f7-list>
          <f7-list-button title="Sign Up" @click.native="createAccount()" close-login-screen></f7-list-button>
          <f7-list-button title="Login" @click.native="$root.reroute('login')"></f7-list-button>
        </f7-list>

        <p>By signing up you agree to the <a class="close-login-screen" @click="$root.reroute('terms')">Terms and conditions</a></p>

        <p>Already have an account? <a class="open-login-screen close-panel">Click here to login</a></p>

    </f7-block>
</f7-page>
</template>

<script>
    
    var noErrors = {
        overall: null,
        first_name: null,
        last_name: null,
        username: null,
        email: null,
        password: null,
        repeat_password: null,
        profile_pic: null
    }
    export default {
        data: function() {
            var data = {
                overall: "",
                first_name: "",
                last_name: "",
                email: "",
                username: "",
                password: "",
                password_repeat: "",
                status: "idle"
            }
            data['errors'] = noErrors
            return data
        },
        methods: {
            validate: function() {
                return new Promise(
                    (resolve, reject) => {
                    var valid = true
                    if (this.first_name == "" ) {
                        this.errors['first_name'] = "First name cannot be empty"
                        valid = false
                    }
                    else {
                        this.errors['first_name'] = ""                        
                    }

                    if (this.last_name == "") {
                        this.errors['last_name'] = "last name cannot be empty"
                        valid = false
                    }
                    else {
                        this.errors['last_name'] = ""                        
                    }

                    if (this.email == "") {
                        this.errors['email'] = "email cannot be empty"
                        valid = false
                    }
                    else {
                        this.errors['email'] = ""                        
                    }

                    if (this.username == "") {
                        this.errors['username'] = "username cannot be empty"
                        valid = false
                    }
                    else {
                        this.errors['username'] = ""                        
                    }

                    if (this.password == "") {
                        this.errors['password'] = "password cannot be empty"
                        valid = false
                    }
                    else if (this.password != this.password_repeat) {
                        this.errors["password"] = "The passwords do not match"
                        this.$root.forceUpdate()
                        valid = false
                    }
                    else {
                        this.errors['password'] = ""
                    }

                    if (valid) {
                        console.log('resolving')
                        this.errors = noErrors
                        resolve("")
                    }
                    else {
                        reject("")
                    }

                })
            },
            createAccount: function() { 
                var ajaxPost = null;
                this.status = "request_sent"
                this.validate().then((res) => {
                    console.log('sending ajax request')
                    ajaxPost = this.$store.dispatch(
                        'user/createAccount',
                        {
                            first_name: this.first_name,
                            last_name: this.last_name,
                            email: this.email,
                            username: this.username,
                            password: this.password,
                        } 
                    )

                    this.status = "user_created"
                    ajaxPost.then((res) => {
                        console.log(res)
                        this.$store.dispatch(
                            'user/authenticate', {
                                username: this.username, 
                                password: this.password
                            }
                        ).then(res => {
                            // better reroute to email confirm page
                            this.status = "login_success"
                            this.errors['overall'] = noErrors                           
                            this.$root.reroute('getstarted')
                        }).catch((err) => {
                            this.errors['overall'] = "Account created but an error occured during login, please login manually"
                        })
                    }).catch((err) => {
                        console.log('err: ')
                        console.log(err)
                        this.handleError(err)
                    })

                }).catch(()=>{
                    console.log('not sending ajax request')
                    this.status = "errors"
                });

            },
            handleError: function(error) {
                if (error.response && error.response.status == 502) {
                  this.errors['overall'] = "Unable to contact server, make sure that you are connected to the internet"
                }
                else if (error.response) {
                  // The request was made and the server responded with a status code
                  // that falls out of the range of 2xx
                  console.log(error.response.data);
                  console.log(error.response.status);
                  console.log(error.response.headers);
                  for (var key in error.response.data) {
                    this.errors[key] = error.response.data[key][0]
                  }

                } else if (error.request) {
                  // The request was made but no response was received
                  // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
                  // http.ClientRequest in node.js
                  this.errors['overall'] = "Unable to contact server, make sure that you are connected to the internet"
                  console.log(error.request);
                } else {
                  // Something happened in setting up the request that triggered an Error
                  this.errors['overall'] = "An unknown error occured, please try again later."
                  console.log('Error', error.message);
                }                
            }
        }
    }
</script>

<style scoped>
    .error {
        color: red;
    }

    #preloader {
        position: absolute;
        left: 49%;
        top: 300px;
    }       
</style>


