import Vue from 'vue'
import Vuex from 'vuex'
import User from './modules/user.js'
import Cart from './modules/cart.js'
import Samples from './modules/samples.js'
import rootGetters from './getters.js'

// Use vuex
Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    user: User,
    samples: Samples,
    cart: Cart
  },
  getters: rootGetters

});








