<template>
    <f7-page>
        <lash-navbar></lash-navbar>
        <f7-block inner>

        <f7-card :inner=false>
            <h1 id="heading">Checkout</h1>
        </f7-card>


        <p>Almost there! One step to go.</p>
        
        <div class="data-table">
            <table>
                <thead>
                    <tr>
                        <th align="center"><big>Item</big></th>
                        <th align="center"><big>Price</big></th>
                    </tr>
                </thead>

                <tr v-for="category in getCategories()">   
                <td colspan="2" class="nopadding">
                    <!-- 
                    Ughhh so we have to resort to inner table hacks
                    in order to be able to use v-for appropriately :#
                    -->
                    <table class="inner">
                        <tbody>
                            <tr align="center" class="highlight">
                                <td colspan=2>{{category}}</td>
                            </tr>
                        </tbody>
                        <tbody v-for="bundle in getBundles(checkoutItems[category], category)">    
                            <tr v-for="bundleItem in bundle" class="shade">
                            <td colspan="2">
                                {{bundleItem.title}}
                            </td>
                            </tr>
                            <tr class="shade2">
                                <th>
                                    Bundle price
                                </th>
                                <th align="right">
                                    £{{ getBundlePrice(category)  }}
                                </th>
                            </tr>
                        </tbody>
                        <tbody>
                            <tr v-for="item in getItems(checkoutItems[category], category)">
                                <td algin="left">{{ item.title }}</td>
                                <td align="right">£{{ item.price }}</td>
                            </tr>
                        </tbody>
                    </table>
                </td>
                </tr>
                <tr>
                    <td colspan="2" class="nopadding">
                    <table>
                        <tbody>
                            <tr class="total">    
                                <td>TOTAL</td>
                                <td align="right">£{{totalPrice()}}</td>
                            </tr>    
                        </tbody>
                    </table>    
                    </td>
                </tr>

            </table>
        </div>

        <p>Click bellow to proceed to payment via PayPal</p>
               
        <!-- <braintree></braintree> -->
        <paypal></paypal>

        </f7-block>
    </f7-page>    
</template>

<script>
    import Vue from 'vue'

    export default {
        data: function() {
            return {
                checkoutItems: {}
            }
        },
        methods: {
            onF7Init: function(f7) {

    console.log('paypal button from checkout page')
    console.log(document.querySelector('#paypal-button'))

                this.updateItems()
            },
            updateItems: function() {
                this.checkoutItems = {}
                var newCheckoutItems = this.$store.getters['cart/byCategory']
                for (var category in newCheckoutItems) {
                    // this.checkoutItems[category] = newCheckoutItems[category]
                    Vue.set(this.checkoutItems, category, newCheckoutItems[category])
                }

            },

            getCategories: function() {
                var categories = []
                for (var category in this.checkoutItems)
                    categories.push(category)
                return categories
            },

            //  Returns the collection of bundles for display in the invoice
            getBundles: function(itemsList, category) {
                var bundleSize = this.getBundleSize(category)
                var bundlePrice = this.getBundlePrice(category)

                var res = []


                var noOfBundles = Math.floor(itemsList.length / bundleSize)

                for (var i=0; i<noOfBundles; i++) {
                    res.push([])
                    for (var j=0; j<bundleSize; j++) {
                        var indexWOffset = i * bundleSize + j
                        res[i].push({
                            title: itemsList[indexWOffset].title,
                            price: bundlePrice
                        })
                    }
                }
                return res

            },
            // returns the collection of individual items for display in the items 
            // (after all the bundles have been collected)
            getItems: function(itemsList, category) {
                var bundleSize = this.getBundleSize(category)
                var unitPrice = this.getUnitPrice(category)
                var remaining = itemsList.length % bundleSize
                var res = []
                for (var i=itemsList.length-1; i>=itemsList.length-remaining; i--) {
                    res.push({
                        title: itemsList[i].title,
                        price: unitPrice
                    })
                }

                return res
            },

            totalPrice: function() {
                return this.$store.getters['cart/price']
            },

            getBundlePrice: function(category) {
                return this.$store.getters['cart/bundlePrice'](category)
            },

            getBundleSize: function(category) {
                return this.$store.getters['cart/bundleSize'](category)
            },

            getUnitPrice: function(category) {
                return this.$store.getters['cart/unitPrice'](category)
            }

        }
    }   
</script>

<style scoped>


    td.nopadding {
        padding: 0;
    }

    tr  {
        background-color: #eee;
    }

    tr.shade {
        background-color: #ddd;
    }

    tr.shade2 {
        background-color: #dedede;
    }

    tr.total {
        color: white;
        background-color: #3494C2;
    }

</style>




