            <h1 id="heading">How it began!</h1>
        </f7-card>
        
        <f7-block inner>
            <p class="center">I​ ​first​ ​took​ ​my​ ​leap​ ​into​ ​the​ ​industry​ ​in​ ​2008,​ ​where​ ​I​ ​took​ ​my​ ​first​ ​ever​ ​course​ ​within eyelash​ ​extensions​ ​with​ ​Wilma​ ​Dunn​ ​known​ ​for​ ​her​ ​thorough​ ​deliverance​ ​of​ ​high standard​ ​training.</p>

            <p class="center">After​ ​attending​ ​this​ ​foundation​ ​course​ ​I​ ​gained​ ​certification​ ​and​ ​began​ ​my​ ​business. During​ ​many​ ​months​ ​of​ ​application​ ​working​ ​on​ ​lots​ ​of​ ​different​ ​ethnic/age​ ​groups​ ​it​ ​was apparent​ ​I​ ​still​ ​had​ ​so​ ​much​ ​to​ ​learn.  Searching​ ​for​ ​many​ ​months​ ​I​ ​had​ ​no​ ​luck,​ ​eye​ ​styling​ ​was​ ​not​ ​available​ ​which​ ​therefore meant​ ​I​ ​had​ ​to​ ​learn​ ​the​ ​hard​ ​way.​ ​That​ ​is​ ​of​ ​course​ ​what​ ​I​ ​choose​ ​to​ ​do! For​ ​many​ ​months​ ​even​ ​years​ ​trying​ ​to​ ​choose​ ​the​ ​right​ ​look,​ ​but​ ​in​ ​actual​ ​fact​ ​wasn't, losing​ ​clients​ ​due​ ​to​ ​incorrect​ ​styling,​ ​losing​ ​time​ ​trying​ ​to​ ​develop​ ​my​ ​skills​ ​it's​ ​fair​ ​to say​ ​it​ ​doesn't​ ​get​ ​any​ ​harder​ ​than​ ​that.​ ​I​ ​Made​ ​it​ ​in​ ​the​ ​end​ ​!! 
            </p>
        </f7-block>

        <f7-block inner>
        <img class="donna" src="../../images/donna1.jpg" alt="image of Donna Bell here">
        </f7-block>

        <f7-block inner>
        <p class="center">
        I​ ​have​ ​since​ ​been​ ​an​ ​educator​ ​for​ ​3​ ​years​ ​and​ ​have​ ​experienced​ ​how​ ​it​ ​is​ ​to​ ​be​ ​the teacher​ ​not​ ​the​ ​leaner​ ​and​ ​there​ ​is​ ​only​ ​so​ ​much​ ​information​ ​that​ ​can​ ​be​ ​delivered​ ​and received​ ​in​ ​the​ ​required​ ​time.  
         As​ ​an​ ​educator​ ​I​ ​realised​ ​what​ ​the​ ​industry​ ​was​ ​missing​ ​,​ ​​ ​that​ ​was​ ​a​ ​guide​ ​to​ ​help​ ​other technicians​ ​like​ ​myself​ ​​ ​solve​ ​indecisive​ ​clients​ ​expectations. 
        </p>
        <p class="center">
         I​ ​am​ ​sure​ ​there​ ​are​ ​many​ ​of​ ​you​ ​that​ ​have​ ​a​ ​client​ ​which​ ​thinks​ ​she​ ​is​ ​a​ ​technician​ ​in her​ ​own​ ​right​ ​and​ ​tells​ ​you​ ​how​ ​to​ ​do​ ​her​ ​lashes​ ​as​ ​that's​ ​what​ ​her​ ​friend​ ​has​ ​or​ ​that's what​ ​the​ ​last​ ​technician​ ​​ ​before​ ​done,​ ​or​ ​you​ ​have​ ​tried​ ​a​ ​look​ ​which​ ​you​ ​thought​ ​was right​ ​for​ ​the​ ​client​ ​but​ ​in​ ​actual​ ​fact​ ​you​ ​should​ ​have​ ​changed​ ​the​ ​curl​ ​of​ ​the​ ​length​ ​in certain​ ​areas.  
        </p>
        <p class="center">
         We​ ​are​ ​artist​ ​in​ ​our​ ​own​ ​industry​ ​we​ ​should​ ​never​ ​be​ ​using​ ​the​ ​same​ ​look​ ​for​ ​every body,​ ​we​ ​should​ ​be​ ​creative​ ​in​ ​the​ ​curls,​ ​lengths​ ​we​ ​have​ ​available​ ​to​ ​us. 
        </p>
         
         <p class="center">
          The​ ​Lash​ ​designer​ ​has​ ​been​ ​developed​ ​to​ ​eliminate​ ​error​ ​of​ ​choice​ ​​ ​and​ ​help​ ​you​ ​get​ ​it right​ ​from​ ​the​ ​get​ ​go. 
         </p>
