import * as config from '../config.js'
import axios from 'axios'
import defaultProfilePic from "../../../images/default-profile.jpg";

export default {
    namespaced: true,    
    state: {
        first_name: '',
        last_name: '',
        username: '',
        email: '',
        token: '',
        profile_pic: '',
        logged_in: false,
        email_confirmed: false,
        account_active: true,
    },
    mutations: {
        updateToken: function(state, token) {
            state.token = token;
        },
        updateUser: function(state, userDetails) {
            state.first_name = userDetails['first_name'];
            state.last_name = userDetails['last_name'];
            state.username = userDetails['username'];
            state.email = userDetails['email'];
            state.profile_pic = userDetails['profile_pic'];
            state.logged_in = true;
        },
        logout: function(state) {
            console.log('logging out')
            // reset the state
            state.first_name = ''
            state.last_name = ''
            state.username = ''
            state.email = ''
            state.token = ''
            state.profile_pic = ''
            state.logged_in = false
            email_confirmed: false
        },
        updateToken: (state, newToken) => {
            console.log('updating token value to: ' + newToken)
            state.token = newToken
            state.logged_in = true
        }        
    },
    actions: {
        authenticate: function(context, deets) {
            var username = deets.username
            var password = deets.password
            console.log(username)
            console.log(password)
            return new Promise(function(resolve, reject) {
                axios.post(`${config.DEV_SERVER}/users/auth_token`, {
                    'username': username,
                    'password': password
                }).then(response => {
                    context.commit('updateToken', response.data.token)
                    context.commit('updateUser', response.data)
                    resolve("");
                }).catch(error => {                    
                    reject(error);
                });
            });
        },
        createAccount: function(context, deets) {
            console.log('creating account!')
            if (context.state.logged_in == true) {
                return new Promise(
                    (resolve, reject) => reject("User logged in")
                );
            }
            var first_name = deets.first_name
            var last_name = deets.last_name
            var username = deets.username
            var email = deets.email
            var password = deets.password

            return new Promise(function(resolve, reject) {
                axios.post(`${config.DEV_SERVER}/users/create`, {
                    'first_name': first_name,
                    'last_name': last_name,
                    'username': username,
                    'password': password,
                    'email': email
                }).then(response => {
                    // context.commit('updateToken', response.data.token)
                    resolve(response);
                }).catch(error => { reject(error) });
            });
        }
    },
    getters: {
        isAuthenticated: state => {
            return state.logged_in
        },
        profilePic: state => {
            var storepfp = state.profile_pic;
            if (storepfp == "") {
              return defaultProfilePic;
            }
            else {
              return storepfp;
            }
        }
    }
}



