import * as config from '../config.js'
import axios from 'axios'

export default {
    namespaced: true,
    state: {
        samples: {},
        samplesFetched: false,
    },
    mutations: {
        update: function(state, newSamples) {
            state.samples = newSamples
            state.samplesFetched = true
        }
    },
    actions: {
        fetch: function(context) {
            if (context.state.samplesFetched === true) {
                return new Promise((resove) => resove())
            }

            console.log('fetching samples')
            console.log(context.rootGetters)
            var token = context.rootGetters.userToken
            console.log(token)
            var extraHeaders =  token!='' ? {Authorization: `Token ${token}`} : {};

            return new Promise(function(resolve, reject) {
                axios.get(`${config.DEV_SERVER}/samples/`, {
                    headers: extraHeaders 
                }).then((response) => {
                    context.commit('update', response.data)                    
                    resolve(response)
                }).catch(err => reject(err));
            })
        }
    },
    getters: {
        categories: function(state) {
            var categories = []
                for (var category in state.samples) {
                    categories.push(category);
                }
            return categories;
        },
        forCategory: function(state) {
            return function(category) {
                return state.samples[category]
            }
        },
        all: function(state) {
            return state.samples
        },
        isLocked: function(state) {
            return function(sample) {
                return !sample.is_free && !sample.is_purchased
            }
        }
    }
}


