// Import Vue
import Vue from 'vue'

// Import Vuex
import store from './assets/vue/store/'

// Import F7
import Framework7 from 'framework7'

// Import F7 Vue Plugin
import Framework7Vue from 'framework7-vue'

// Import F7 iOS Theme Styles
/* eslint-disable global-require */
// if (window.isiOS) {
  const Framework7Theme =
    require('framework7/dist/css/framework7.ios.min.css');
  const Framework7ThemeColors =
    require('framework7/dist/css/framework7.ios.colors.min.css');
// } else {
//   /* OR for Material Theme: */
//   const Framework7ThemeMaterial =
//     require('framework7/dist/css/framework7.material.min.css');
//   const Framework7ThemeColorsMaterial =
//     require('framework7/dist/css/framework7.material.colors.min.css');
// }

 // OR for Material Theme:
 // import Framework7Theme from 'framework7/dist/css/framework7.material.min.css'
 // import Framework7ThemeColors from 'framework7/dist/css/framework7.material.colors.min.css'

 // lib
 // import Recorder from './assets/lib/recorder.js'


// Import Images
import logoImg from './assets/images/logo_blue.png'
console.log(logoImg)

 // Import
// import f7eot from "framework7-icons/fonts/Framework7Icons-Regular.eot"
// import f7woff from "framework7-icons/fonts/Framework7Icons-Regular.woff"
// import f7woff2 from "framework7-icons/fonts/Framework7Icons-Regular.woff2"
// import f7tiff from "framework7-icons/fonts/Framework7Icons-Regular.ttf"
// console.log(f7tiff);

// fonts
// import 'assets/fonts/Oswald-Regular.ttf';


// Import App Custom Styles
require('./assets/sass/main.scss')
require('./assets/sass/lash.scss')
require('framework7-icons/css/framework7-icons.css')
 

// Import Routes
import Routes from './routes.js'

// Import App Component
import App from './main.vue'

// Import other components
import jscolorComp from './assets/vue/components/jscolor.vue'
import braintreeComp from './assets/vue/components/braintree.vue'
import paypalComp from './assets/vue/components/paypal.vue'
import lashSwiper from './assets/vue/components/lash-swiper.vue'
import lashNavbar from './assets/vue/components/lash-navbar.vue'
import lashLogin from './assets/vue/pages/login.vue'
import lashCart from './assets/vue/pages/cart.vue'
import lashLeftNav from './assets/vue/components/lash-leftnav.vue'
import lashSamples from './assets/vue/components/lash-samples.vue'
import CamBtn from './assets/vue/components/cam-btn.vue'

// vendor scripts
// import clm from './assets/js/vendor/clmtrackr/clmtrackr.js';
import {fabric} from '../node_modules/fabric/dist/fabric.js';
import Hammer from './assets/js/vendor/hammerjs/hammer.js';


import './assets/js/vendor/jscolor/jscolor.js'
const jscolor = window.jscolor

// Init F7 Vue Plugin
Vue.use(Framework7Vue)


// define components
Vue.component('jscolor', jscolorComp)
Vue.component('braintree', braintreeComp)
Vue.component('paypal', paypalComp)
Vue.component('lash-login', lashLogin)
Vue.component('lash-cart', lashCart)
Vue.component('lash-swiper', lashSwiper)
Vue.component('lash-navbar', lashNavbar)
Vue.component('lash-leftnav', lashLeftNav)
Vue.component('lash-samples', lashSamples)
Vue.component('cam-btn', CamBtn)


// Init App
var app = new Vue({
  el: '#app',
  store: store,
  data: {
    images: {
      logo: logoImg
    },
    fabric: fabric,
    jscolor: jscolor
  },
  template: '<app/>',
  // Init Framework7 by passing parameters here
  framework7: {
    root: '#app',
    /* Uncomment to enable Material theme: */
    // material: true,
    routes: Routes
  },
  // Register App Component
  components: {
    app: App
  },
  methods: {
    reroute: function(newroute) {
      console.log(newroute);
      if (newroute === 'home') {
        // window.location.reload();
        this.$f7.mainView.router.back({pageName:'index', force:true})
      }
      else {
        this.$f7.mainView.router.load({url: '/' + newroute});
      }
    },
    visitBlog: function() {
      console.log(window);
      window.open('https://donnabelllashes.wordpress.com/', '_system');
    }
  }
})






