<template>
  <f7-page login-screen>
    <f7-preloader id="preloader" v-if="status=='request_sent'"></f7-preloader>
    <!-- <a class="close-login-screen" href="#">X</a> -->
    <f7-link id="close" class="close-login-screen" :close-popup="true">
        <span class="icon-cell"><i class="f7-icons size-50">close</i></span>
    </f7-link>

    <f7-block>

        <f7-login-screen-title>Login</f7-login-screen-title>
        
        <p>Login to purchase samples, view or orders, and access purchased samples.</p>

        <!-- Error messages -->
        <f7-block class="errorMessage" v-if="errorMessage!=''">
          {{errorMessage}}
        </f7-block>
        <f7-block class="successMessage" v-if="successMessage!=''">
          {{successMessage}}
        </f7-block>

        <f7-list form>
          <f7-list-item>
            <f7-label>Username</f7-label>
            <f7-input name="username" placeholder="Username" type="text" v-model="username"></f7-input>
          </f7-list-item>
          <f7-list-item>
            <f7-label>Password</f7-label>
            <f7-input name="password" type="password" placeholder="Password" v-model="password"></f7-input>
          </f7-list-item>
        </f7-list>
        <f7-list>
          <f7-list-button title="Sign In" @click.native="authenticate"></f7-list-button>
        </f7-list>

        <p>Don't have an account? <a class="close-login-screen" @click="$root.reroute('register')" href="">Click here to sign up</a></p>

        <p>Forgot your password? <a class="close-login-screen" @click="$root.reroute('forgotpass')">click here to recover</a></p>
    </f7-block>
  </f7-page>
</template>

<style scoped>
    #preloader {
        position: absolute;
        left: 49%;
        top: 300px;
    }   

    .errorMessage {
        color: red;
    }

    .successMessage {
        color: green;
    }

    #close {
        position: absolute;
        right: 15px;
        top: 15px;
    }

</style>

<script>
    export default {
        data: function() {
            return {
                username: "",
                password: "",
                status: "idle", // request_sent // request_received
                errorMessage: "",
                successMessage: "",
            }
        },
        methods: {
            authenticate: function() {
                var self = this;
                self.$f7.showPreloader('logging in...');
                this.$store.dispatch(
                    'user/authenticate', 
                    {
                        username: this.username, 
                        password: this.password
                    }
                ).then((res) => {
                    self.$f7.hidePreloader();                    
                    console.log('login succeeded!')
                    this.status = "request_received";
                    this.errorMessage = ""
                    this.successMessage = "Login succeeded"
                    // this.$root.reroute('getstarted')    
                    window.setTimeout(()=>
                        self.$f7.closeModal("#login-screen"), 500)

                }).catch((err) => {
                    self.$f7.hidePreloader();                    
                    console.log('login failed!!')
                    console.log(err)
                    this.status = "request_received";
                    this.handleError(err)
                    this.successMessage = ""
                })
                this.status = "request_sent";
            },
            goToRegistration: function() {
                this.$root.navigate('register')
            },
            handleError: function(error) {
                if (error.response && error.response.status == 502) {
                  this.errorMessage = "Unable to contact server, make sure that you are connected to the internet"
                }
                else if (error.response) {
                  // The request was made and the server responded with a status code
                  // that falls out of the range of 2xx
                  this.errorMessage = "Error: Invalid username or password"
                  console.log(error.response.data);
                  console.log(error.response.status);
                  console.log(error.response.headers);
                } else if (error.request) {
                  // The request was made but no response was received
                  // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
                  // http.ClientRequest in node.js
                  this.errorMessage = "Unable to contact server, make sure that you are connected to the internet"
                  console.log(error.request);
                } else {
                  // Something happened in setting up the request that triggered an Error
                  this.errorMessage = "An unknown error occured, please try again later."
                  console.log('Error', error.message);
                }                
            }
        }
    }
</script>




