
import * as config from '../config.js'

const PRICES = {
    'Classic': {
        unit_price: 1.49,
        bundle_price: 4.99,
        bundle_quantity: 4
    },
    'Xd': {
        unit_price: 2.49,
        bundle_price: 8.99,
        bundle_quantity: 4       
    },
    'Russian': {
        unit_price: 2.49,
        bundle_price: 8.99,
        bundle_quantity: 4
    },
    'Hybrid': {
        unit_price: 2.49,
        bundle_price: 4.99,
        bundle_quantity: 4        
    },
    'MegaV': {
        unit_price: 2.49,
        bundle_price: 8.99,
        bundle_quantity: 2        
    },
    'Artistic': {
        unit_price: 2.49,
        bundle_price: 8.99,
        bundle_quantity: 2        
    }
}

export default {
    namespaced: true,
    state: {
        items: [],
    },
    mutations: {
        update: function(state, items) {
            console.log('items: ')
            console.log(items)
            state.items = items
        }
    },
    actions: {
        
    },
    getters: {
        items: function(state) {
            return state.items
        },
        price: function(state) {
            var catCounts = {
            }
            for (var i=0; i<state.items.length; i++) {
                if (state.items[i].category in catCounts) {
                    catCounts[state.items[i].category] += 1
                }
                else {
                    catCounts[state.items[i].category] = 1
                }
            }

            var price = 0
            console.log('category couunts')
            console.log(catCounts)
            for (var category in catCounts) {
                var bundle_price = PRICES[category].bundle_price
                var bundle_qty = PRICES[category].bundle_quantity
                var unit_price = PRICES[category].unit_price

                console.log(price)
                price = price + 
                            ((catCounts[category] % bundle_qty) * unit_price) + 
                            (Math.floor(catCounts[category] / bundle_qty) * bundle_price)
            }

            return price.toFixed(2)
        },
        byCategory: function(state) {
            var res = {}
            for (var i=0; i<state.items.length; i++) {
                var category = state.items[i].category
                if (category in res) {
                    res[category].push(state.items[i])
                }
                else {
                    res[category] = [ state.items[i] ]
                }
            }
            return res
        },

        bundlePrice: function(state) {
            return function(category) {
                return PRICES[category].bundle_price
            }
        },

        bundleSize: function(state) {
            return function(category) {
                return PRICES[category].bundle_quantity
            }
        },

        unitPrice: function(state) {
            return function(category) {
                return PRICES[category].unit_price
            }
        }
    }
}


