<template>



    <!-- <f7-popup id="cart-screen"> -->
    <!-- <div class="page navbar-fixed"> -->
        <!-- <f7-navbar class="navbar-inner" title="Shopping Cart"> -->
            <!-- <a href="#" id="x" class="close-popup">X</a> -->
        <!-- </f7-navbar> -->
    <f7-page>
        <lash-navbar></lash-navbar>

        <a id="checkout-bar" href="#" 
           class="floating-button floating-button-to-popover open-popover color-purple"
           :class="{active: price>0}"
           :disabled="price ==0 "
           @click="goToCheckout()">
          Total Price: {{price}} <br>
          Proceed to checkout >
        </a>

        <!-- <div class="page-content"> -->

            <f7-card :inner=false>
                <h1 id="heading">Shopping Cart</h1>
            </f7-card>

            <f7-block>
                <p>Save money by purchasing your lashes in bundles!</p>
                <div class="data-table">
                    <table class="pricelist">
                        <thead class="header">
                            <tr>
                                <th align="center">Category</th>
                                <th>Unit Price</th>
                                <th>Bundle Price</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td align="center">Classic</td>
                                <td>£1.49</td>
                                <td>4 for £4.99</td>
                            </tr>
                            <!-- <tr>
                                <td align="center">XD</td>
                                <td>£2.49</td>
                                <td>4 for £8.99</td>
                            </tr> -->
                            <tr>
                                <td align="center">Mega</td>
                                <td>£2.49</td>
                                <td>2 for £8'.99</td>
                            </tr>
                            <tr>
                                <td align="center">Artistry</td>
                                <td>£2.49</td>
                                <td>2 for £4.99</td>
                            </tr>
                            <tr>
                                <td align="center">Hybrid</td>
                                <td>£2.49</td>
                                <td>4 for £4.99</td>
                            </tr>
                            <tr>
                                <td align="center">Russian</td>
                                <td>£2.49</td>
                                <td>4 for £8.99</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </f7-block>

            <f7-block inner v-for="category in categories" >
                <!-- Begin samples here for purchase -->
                <p>{{category}}</p>
                <div id="swiper-container" class="swiper-container cart_swiper">
                    <!-- <div class="swiper-pagination"></div> -->
                    <div class="swiper-wrapper">
                        <div class="swiper-slide"
                              v-for="sample in samples[category]"
                              @click="toggleSelection(sample)"
                              v-if="isLocked(sample)"
                              :class="{selected: sample.is_selected}">

                          <span class="label" id="label">{{sample.title}} | £{{getUnitPrice(category)}}</span>
                          <!-- <span class="label" id="label2">£{{getUnitPrice(category)}}</span> -->
                          <img class="preview" :src="sample.lash_preview"></img>

                        </div>

                        <div class="swiper-slide"><span>Lash 2</span></div>
                        <div class="swiper-slide"><span>Lash 3</span></div>
                        <div class="swiper-slide"><span>Lash 4</span></div>
                        <div class="swiper-slide"><span>Lash 5</span></div>
                        <div class="swiper-slide"><span>Lash 6</span></div>
                        <div class="swiper-slide"><span>Lash 7</span></div>
                        <div class="swiper-slide"><span>Lash 8</span></div>
                        <div class="swiper-slide"><span>Lash 9</span></div>
                        <div class="swiper-slide"><span>Lash 10</span></div> 
                    </div>
                </div>

            </f7-block>
            <div class="spacing"></div>
        <!-- </div> -->
    </div>
    </f7-page>

    <!-- </f7-popup> -->

</template>

<script>

    import Vue from 'vue'

    export default {
        data: function() { 
            return {
                slider: null,
                samples: {},
                categories: [],
                price: 0
            }
        },
        methods: {
            initSwipers: function() {
                var self = this;
                self.slider = this.$f7.swiper('.cart_swiper', {
                    // direction: 'horizontal',
                    speed: 500,
                    spaceBetween: 10,
                    width: 140,
                    // height: 120,
                    // slidesPerView: 3,
                    setWrapperSize: true,
                    pagination:'.swiper-pagination',
                    paginationHide: false,
                    paginationClickable: true,
                    nextButton: '.swiper-button-next',
                    prevButton: '.swiper-button-prev',                    
                    autoplayDisableOnInteraction: false,
                    observer: true
                }); 

            },
            onF7Init: function(f7) {
                var self = this
                self.initSwipers()

                window.setInterval(() => self.initSwipers(), 4000)

                this.$store.dispatch('samples/fetch').then((response) => {
                    this.samples = this.$store.getters['samples/all']
                    self.loadCategories()



                }).catch((err) => {
                    this.$f7.hidePreloader()
                })
            },
            loadCategories: function() {
                this.categories = []
                var loadedCategories = this.$store.getters['samples/categories']
                for (var i=0; i<loadedCategories.length; i++)  {
                    this.categories.push(loadedCategories[i])
                }
            },
            isLocked: function(sample) {
                return this.$store.getters['samples/isLocked'](sample)
            },
            toggleSelection: function(sample) {
                // We need to use Vue.set to make this reactive
                Vue.set(sample, 'is_selected', !sample.is_selected)
                // sample.is_selected = !sample.is_selected;
            },
            goToCheckout: function() {
                // this.$root.reroute('checkout')
                this.$f7.closeModal("#cart-screen", false)
                this.$root.reroute('checkout')
            },
            getUnitPrice: function(category) {
                return this.$store.getters['cart/unitPrice'](category)
            }            
        },
        computed: {
            cartItems: function() {
                var res = []
                for (var category in this.samples) {

                    for (var i=0; i<this.samples[category].length; i++) {
                        var sample = this.samples[category][i]
                        if (sample.is_selected) {
                            res.push(sample)
                        }
                    }
                }
                return res
            },

        },
        watch: {
            cartItems: function() {
                console.log('cart items:')
                console.log(this.cartItems)
                this.$store.commit('cart/update', this.cartItems)
                this.price = this.$store.getters['cart/price']
            }
        }

    }
    
</script>

<style scoped>

    table {
        background-color: #eee;
    }

    table.pricelist thead.header th {
        background-color: #3494C2;
        color: white;
    }

    .navbar-inner {
        text-align: center;
        color: white;
    }

    #swiper-container {
        /*width: 100%;*/
        overflow-y: auto;
    }

    #checkout-bar.active {
        top: 50px;
    }

    #x  {
        color: white;
        position: absolute;
        right: 15px;
        top: 15px;
    }

    .floating-button {
        width: 100%;
        border-radius: 0;
        right: 0;
    }

    .spacing {
        height: 50px;
    }

    .cartCheck {
        position: absolute;
        top: 10px;
        right: 10px;
    }

    .swiper-slide {
        width:118px;
        height: 130px;
        margin: 0 auto;
        opacity: 80%;
        border: none;    
        border: 2px solid #ccc;    
    }

    .swiper-slide.selected {
        border: 3px solid blue;
        opacity: 100%;
    }

    .swiper-slide .label {
        z-index: 999;
        font-size: 10px;
        margin: 0;
        position: absolute;
        color: blue;
        left: 5px;
    }

    .swiper-slide #label {
        z-index: 999;
        font-size: 10px;
        margin: 0;
        position: absolute;
        bottom: 3px;
        color: blue;
    }

    .swiper-slide #label2 {
        bottom: 2px;
    }

    .preview {
        position: relative;
        top: 0;
        left: 0;
        width: 100%;
        border-bottom: 2px solid #ccc;
    }

</style>


