<template>
<div>      

    <div id="lashes">
        <f7-list form v-if="status=='categories'" >

          <f7-list-item 
              v-for="category in categories" 
              type="radio" 
              :name="category" 
              :title="category" 
              @click="loadCategory(category)">
          </f7-list-item>

          <!-- <f7-list-item radio name="category" value="1" title="Classic" @click="loadCategory('Classic')"></f7-list-item>
          <f7-list-item radio name="category" value="2" title="Russian" @click="loadCategory('Russian')"></f7-list-item>
          <f7-list-item radio name="category" value="3" title="Hybrid" @click="loadCategory('Hybrid')"></f7-list-item>
          <f7-list-item radio name="category" value="3" title="MegaV" @click="loadCategory('MegaV')"></f7-list-item>
          <f7-list-item radio name="category" value="3" title="Artistic" @click="loadCategory('Artistic')"></f7-list-item> -->
        </f7-list>

        <div v-show="status=='samples'">
            <f7-list>
                <f7-list-item title="< Back" @click="status='categories'"></f7-list-item>
            </f7-list>

            <div id="swiper-container" class="swiper-container swiper-3 getstarted_swiper">
                <!-- <div class="swiper-pagination"></div> -->
                <div class="swiper-wrapper">

                    <div class="swiper-slide"
                          v-for="sample in currentSamples"
                          @click="useOrPurchase(sample)">
                      <span>{{sample.title}}</span><br>
                      <img class="preview" :src="sample.lash_preview"></img>
                      <img class="lock" v-if="isLocked(sample)" src="../../images/lock.svg"></img>
                    </div>

                    <!-- <div class="swiper-slide">
                        <img @click="emitEvent" src="../../images/lash_example.jpg" style="width: 100%">
                    </div> -->

                    <div class="swiper-slide"><span>Lash 2</span></div>
                    <div class="swiper-slide"><span>Lash 3</span></div>
                    <div class="swiper-slide"><span>Lash 4</span></div>
                    <div class="swiper-slide"><span>Lash 5</span></div>
                    <div class="swiper-slide"><span>Lash 6</span></div>
                    <div class="swiper-slide"><span>Lash 7</span></div>
                    <div class="swiper-slide"><span>Lash 8</span></div>
                    <div class="swiper-slide"><span>Lash 9</span></div>
                    <div class="swiper-slide"><span>Lash 10</span></div> 
                </div>
            </div>
        </div>
    </div>

</div>
        
</template>

<script>
import lashimg from '../../images/sample_1.svg'

    module.exports = {
        methods: {
            rerenderSlider: function() {
                this.slider = this.$f7.swiper('.getstarted_swiper', {
                    speed: 500,
                    spaceBetween: 10,
                    width: 118,                    
                    // slidesPerView: 3,
                    pagination:'.swiper-pagination',
                    paginationHide: false,
                    paginationClickable: true,
                    nextButton: '.swiper-button-next',
                    prevButton: '.swiper-button-prev',                    
                    autoplayDisableOnInteraction: false,
                });
            },
            onF7Init: function(f7) {
                var self = this;
                this.rerenderSlider();
                window.setInterval(() => self.rerenderSlider(), 4000)

                console.log(this.slider);
                // this.slider.startAutoplay();

                // Fetch the current samples
                console.log('dispatching sample fetch')
                this.$store.dispatch('samples/fetch').then((response) => {
                    console.log(response)
                    // this.categories = this.$store.sample.getters['samples/catgories']
                    console.log(this.$store.getters['samples/categories'])
                    console.log(this.categories)
                }).catch(err => {
                    console.log(err)
                });

                 // The swiper height needs to match the 4/3 aspect ratio of the captured images
                document.querySelector("#swiper-container").style.height = screen.width * 3 / 4 + "px";
            },
            useOrPurchase: function(sample) {
              var self = this;
              if (this.isLocked(sample)) {
                // view login screen or redirect to cart
                console.log('locked ..')
                if (this.$store.getters['user/isAuthenticated']) {
                  self.$root.reroute("cart")
                }
                else {
                  self.$f7.loginScreen("#login-screen");
                }
              }
              else {
                console.log('lash preview happening now')
                // this.$emit('newlash', lashimg);
                this.$emit('newlash', sample.lash_vector);
              }
            },
            isLocked: function(sample) {
              return this.$store.getters['samples/isLocked'](sample)
            },
            emitEvent: function() {
                this.$emit('newlash', lashimg);
            },
            loadCategory: function(category) {
                var self = this;
                console.log('new category: ' + category)
                this.status = 'samples'
                this.currentCategory = category
                console.log(this.currentSamples)
                // self.slider.update(true)
            }
        },
        created: function() {
            // console.log('I have been created!');
            // console.log(this.$f7);

            // document.querySelector("swiper");
        },
        data: function() {
            return {
                status: 'categories',
                f7: null,
                slider: null,
                currentCategory: ""
            }
        },
        computed: {
          categories: function() {
            return this.$store.getters['samples/categories']
          },
          currentSamples: function() {
            if (this.currentCategory != "") {
              return this.$store.getters['samples/forCategory'](this.currentCategory)
            }
            else {
              return []
            }
          }
        }
    }
</script>

<style scoped>
    #swiper-container {
        /*width: 100%;*/
        overflow-y: auto;
    }


    .hidden {
      display: hidden;
    }


    .swiper-slide {
      box-sizing: border-box;
        width:118px;
        height: 89px;
        margin: 0 auto;
        opacity: 80%;
        border: none;    
        border: 2px solid #ccc;    
    }

    .swiper-slide.selected {
        border: 3px solid blue;
        opacity: 100%;
    }

    .swiper-slide span {
      text-align:center;
      display:block;
      margin: 20px;
      font-size:21px;
    }

    #swiper-container {
      height: 120px;
      margin: 10px 0;
    }

    .swiper-wrapper {
        height: 100px;
    }

    #lashes {
        width: 100%;
        overflow: auto;
    }

    .lock {
      position: absolute;
      top: -0;
      right: -0;
      width: 30px;
    }

    .preview {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: auto;
    }

</style>


