<template>
	<f7-page>
        <lash-navbar></lash-navbar>
		<!-- <f7-navbar title="About" back-link="Back" sliding></f7-navbar> -->

        <f7-card :inner=false>
        <h1 id="heading">Let’s Style !!</h1>
        </f7-card>

		<div id="preview">
            <canvas id="canvas" ></canvas>
            <!-- <canvas id="overlay"></canvas> -->
		</div>


        <f7-button id="recapture" @click.native="captureImage">Recapture image</f7-button>

        <jscolor id="jscolor_main" value="000000" v-on:input="updateLashColour" ></jscolor>

        <lash-samples v-on:newlash="addSample"></lash-samples>

<!-- 		<f7-card>
			<f7-block style="padding: 10px" > 
				<cam-btn style="margin: 0 auto" @click.native="launchCam"></cam-btn>
			</f7-block>
		</f7-card>
 -->

	</f7-page>

</template>

<style>

    #heading {
        text-align: center;
    }

    #preview {
        border-bottom: dashed 2px #ddd;
    }

    #canvas {
        width: 100%;
        margin: 0 auto;
    }

    canvas#overlay {
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
    }

    .list-block {
        margin: 0!important;
    }

    #instruction {
        padding: 0;
        margin: 5px 0;
        line-height: 20px;
        text-align: center;
    }


</style>

<script>

import config from '../../../config.js';
//temp


export default {
    name: "desktop",
    data: function() {
        return {
            fabric: this.$root.fabric,
            canvas: null,
            lashColor: "#000000"
        }
    },
    created: function() {
    },
    methods: {
        onF7Init: function(){
            this.rerender();
        },
        rerender: function() {
            // assuming a portrait layout        
            // the height of the instructions element
            var instructionHeight = 25;
            // subtract 40 for navbar height and the instructions element height
            var availableheight = window.innerHeight 
                                - config.F7_NAVBAR_HEIGHT - instructionHeight;
            var canvas = document.querySelector("#canvas");//.style.height;// = availableheight  * 0.8 + 'px';
            // var canvasOverlay = document.querySelector("#overlay");
            var lashes = document.querySelector("#lashes");//.style.height;// = availableheight * 0.2 + 'px';

            var canvasHeight = window.innerHeight * 0.4;
            canvas.style.height = canvasHeight + 'px'; //availableheight * 0.40 + 'px';
            // canvasOverlay.style.height = canvas.style.height;
            // canvasOverlay.style.width = window.innerWidth;

            this.canvas = new this.fabric.Canvas('canvas');
            this.canvas.setHeight(canvasHeight);
            this.canvas.setWidth(parseFloat(window.innerWidth));

            // Disable gorup selection (messes with panning)
            this.canvas.selection = false;


            // lashes.style.height = availableheight - canvas.style.height - 20 + 'px'; //availableheight * 0.35 + 'px';

            // this is important to allow the page to scroll
            // see https://github.com/kangax/fabric.js/issues/1864
            this.canvas.allowTouchScrolling = true;


            // events for canvas zoming and panning
            this.canvasPanZoom();
        },
        canvasPanZoom: function() {
            var $$ = window.Dom7;
            var fabric = this.fabric;
            var canvas = this.canvas;
            var pausePanning = false;
            var currentZoomScale = 1,
                lastX =0,
                lastY = 0,
                currentX,
                currentY;

            const max_zoom = 4;
            const min_zoom = 1;


            var el = document.querySelector("#preview");
            var hammertime = new Hammer(el);
            hammertime.get('pinch').set({ enable: true });

            hammertime.on('pinch', function(ev) {

                if (!pausePanning) {
                    var point = new fabric.Point(ev.center.x, ev.center.y)
                    var zoomFactor = ev.additionalEvent == 'pinchout' ? ev.scale*0.1 :  -ev.scale*0.3;
                    var newZoom = currentZoomScale + (zoomFactor);

                    if (newZoom >= min_zoom && newZoom  <= max_zoom) {
                        canvas.zoomToPoint(point, newZoom);
                        currentZoomScale = newZoom;
                        pausePanning = false;  
                        canvas.renderAll();
                    }
                }
            });

            hammertime.on('pan', function(ev) {
                if (!pausePanning) {
                    var changeX = ev.deltaX - lastX;
                    var changeY = ev.deltaY - lastY;
                    if (Math.abs(changeX) <= 20 && Math.abs(changeY) <= 20) {
                        canvas.relativePan(new fabric.Point(changeX, changeY));
                    }
                    lastX = ev.deltaX;
                    lastY = ev.deltaY;
                }
                canvas.renderAll();

            });

            canvas.on({
                'object:selected': function() {
                    pausePanning = true;
                },
                'selection:cleared': function() {
                    pausePanning = false;
                }
            })
       
        },
        captureImage: function() {
            // display notification on whehter to use cam or gallery
            var target = "#recapture";
            var self = this;
            var buttons = [
                {
                    text: 'Use Camera',
                    onClick: () => self.launchCam('camera')
                },
                {
                    text: 'Import From Gallery',
                    onClick: () => self.launchCam('gallery')
                }
            ];
            this.$root.$f7.actions(target, buttons, true);
        },
        launchCam: function(mediaType) {
            var self = this;
            // 1 for camera, 0 for gallery
            if (mediaType != 'camera' && mediaType != 'gallery') {
            }
            var  source = mediaType == 'camera' ? 1 : 0;
            var thumbHeight = parseFloat(canvas.style.height);
            navigator.camera.getPicture( 
                function(path) {
                    var canvasEl = document.querySelector('#canvas');
                    var canvas = self.canvas;

                    // create image object to get the image size ..
                    var img1 = new Image();
                    img1.onload =function() {
                        var scaleFactor = window.innerWidth/img1.width;window.innerWidth;
                        canvas.setBackgroundImage(path, canvas.renderAll.bind(canvas), {
                            top: 0,
                            left: 0,
                            originX: 'left',
                            originY: 'top',
                            scaleX: scaleFactor,
                            scaleY: scaleFactor
                        });
                        
                    }
                    img1.src = path;

                },
                function(err) {}, 
                {
                    cameraDirection: 1, 
                    correctOrientation: true, 
                    destinationType: 1,
                    sourceType: source
                }
            )            
        },
        importFromGallery: function() {


        },
        addSample: function(newPath) {
            var self = this
            var canvas = this.canvas;
            var scaleX = canvas.viewportTransform[0];
            var scaleY = canvas.viewportTransform[3];
            var vx = canvas.viewportTransform[4];
            var vy = canvas.viewportTransform[5];
            self.fabric.loadSVGFromURL(newPath, function(objects, options) {
                self.lashVectorObj = fabric.util.groupSVGElements(objects, options);

                self.lashVectorObj.set('left', vx*-1/scaleX);
                self.lashVectorObj.set('top', vy*-1/scaleY);
                self.lashVectorObj.scale(100/self.lashVectorObj.width/scaleX); 
                canvas.add(self.lashVectorObj)                
                

                self.updateLashColour(document.querySelector("#jscolor_main").value)
                canvas.renderAll()
            });
        },
        updateLashColour: function(newvalue) {
            console.log('updating lash color to: ' + newvalue)
            if (!this.lashVectorObj) {
                return;
            }

            console.log(newvalue)
            newvalue = "#" + newvalue
            if (this.lashVectorObj.isSameColor && this.lashVectorObj.isSameColor() || !this.lashVectorObj.paths) {
              this.lashVectorObj.setFill(newvalue);
            }
            else if (this.lashVectorObj.paths) {
              for (var i = 0; i < this.lashVectorObj.paths.length; i++) {
                this.lashVectorObj.paths[i].setFill(newvalue);
              }
            }
            this.canvas.renderAll()
        }
    }
}

</script>




